import React, { useEffect, useState } from "react";
import { UserById } from "../../services/api/user";
import ProfileTab from "../../components/Profile/ProfileTab";
import Loader from "../../components/loader";
import Popup from "../../components/Modal/Popup";
import TxnList from './policy-holder-list'
const DashProject = () => {
  const [isLoader, setIsLoader] = useState(true);
  let pId = sessionStorage.getItem("pId");
  const [popupId, setPopupId] = useState(pId);
  const [userData, setUserData] = useState();
  let userId = sessionStorage.getItem("userId");
  useEffect(() => {
    async function getData() {
      setIsLoader(true);
      let response = await UserById(userId);
      setUserData(response?.data?.data);
      setIsLoader(false);
    }
    getData();
  }, []);

  return (
    <>
      {popupId ? <Popup setPopupId={setPopupId} /> : null}
      
      {isLoader ? (
        <Loader loaded={isLoader} />
      ) : (
        <div className="dashboard-section">
          <ProfileTab setIsLoader={setIsLoader} />
          <TxnList />
        </div>
      )}
    </>
  );
};

export default DashProject;
