import React, { useState, useEffect } from "react";
import { Row, Col, Card, Dropdown, Button } from "react-bootstrap";
import avatar2 from "../../assets/images/user/avatar-2.jpg";
import { UserById } from "../../services/api/user";
import { CONSTANTS } from "../../config/dataConstant";
import Loader from "../loader";

function ProfileTab({ urlUserId }) {
  let userId = sessionStorage.getItem("userId");

  
  const [userData, setUserData] = useState();
  const [isLoader, setIsLoader] = useState(true);


  useEffect(() => {
    async function getData() {
      setIsLoader(true);
      let response = await UserById(urlUserId ? urlUserId : userId);
      setUserData(response?.data?.data);
      setIsLoader(false);
    }
    getData();
  }, []);

  return (
    <>
      
      {isLoader ? (
        <Loader loaded={isLoader} />
      ) : (
        <Card className="user-profile-card  mb-4 p-0 ">
          <Card.Header className="border-0 p-0 mt-4 mr-4 border d-flex justify-content-end">
            
          </Card.Header>
          <Card.Body className="py-0">
            <div className="user-about-block m-0">
              <Row>
                <Col md={12} className="text-center mb-5 ">
                  <div className="change-profile text-center">
                    <Dropdown className="w-auto d-inline-block">
                      <Dropdown.Toggle
                        as="a"
                        variant="link"
                        id="dropdown-basic"
                      >
                        <div className="profile-dp">
                          <div className="position-relative d-inline-block">
                            <img
                              className="img-radius img-fluid wid-100"
                              src={avatar2}
                              alt="User"
                            />
                          </div>
                        </div>
                      </Dropdown.Toggle>
                    </Dropdown>
                  </div>
                  <h5 className="mb-1 text-capitalize font-weight-bold">
                    {userData?.name}
                  </h5>
                  <p className="mb-2 text-muted">
                    {CONSTANTS.formFields.id} {userData?.username}
                  </p>
                </Col>
              </Row>
            </div>
          </Card.Body>
        </Card>
      )}
    </>
  );
}

export default ProfileTab;
