import React, { useState, useEffect } from "react";
import { Row, Col, Card } from "react-bootstrap";
import makeData from "../policy-holder/tableData";
import Table from "../../components/Table/table";
import Loader from "../../components/loader";
import { CONSTANTS } from "../../config/dataConstant";
import { GetListNextDue } from "../../services/api/police-holder";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

const TxnHistory = () => {
  const [isLoader, setIsLoader] = useState(true);
  const [txnData, setTxnData] = useState();
  const history = useHistory()

  useEffect(() => {
    async function fetchData() {
      let res = await GetListNextDue();
      console.log(res?.data);
      setTxnData(res?.data?.data?.data);
      setIsLoader(false);
      console.log(txnData);
    }
    fetchData();
  }, []);
  
  const cmsPageDetails = (cmsPage) => {
    if (cmsPage) {
      const route = "/policy-holder/details?edit=true&tab=0&id=" + cmsPage?._id;
      history.push(route);
    }
  }
  
  const columns = React.useMemo(
    () => [
      {
        Header: CONSTANTS.policyHolder.TABLE.HEADER.SNO,
        accessor: "sno",
      },
      {
        Header: CONSTANTS.policyHolder.TABLE_PAGE.TITLE_HEADER,
        accessor: "name",
      },
      {
        Header: CONSTANTS.policyHolder.TABLE_PAGE.POLICY_NO,
        accessor: "policyNo",
      },
      ,
      {
        Header: CONSTANTS.policyHolder.TABLE_PAGE.NEXT_DUE,
        accessor: "nextDue",
      },
      {
        Header: CONSTANTS.policyHolder.TABLE_PAGE.TYPE_OF_PAY,
        accessor: "typeOfEmi",
      },
      {
        Header: CONSTANTS.policyHolder.TABLE_PAGE.CREATED_AT,
        accessor: "createdAt",
      },
    ],
    []
  );
  const data = React.useMemo(() => makeData(txnData?.length, txnData, cmsPageDetails));

  return (
    <React.Fragment>
      {isLoader ? (
        <Loader loaded={isLoader} />
      ) : (
        <Row>
          <Col>
            <Card>
              <Card.Body>
                {txnData?.length ? (
                  <Table columns={columns} data={data} />
                ) : (
                  <h3 className="text-muted">
                    {CONSTANTS.transactionTable.noHistoryMsg}
                  </h3>
                )}
              </Card.Body>
            </Card>
          </Col>
        </Row>
      )}
    </React.Fragment>
  );
};

export default TxnHistory;
